.revokeUserWrapper {
    display: flex;
    flex-direction: column;
    padding-left: 83px;
    padding-top: 23px;
}

textarea {
    width: 40%;
}

.revokeUserSelectDiv {
    display: flex;
    flex-direction: column;
}

.selectRevokeUser {
    width: 40%;
}

.revokeUserBtn {
    color: #fff;
    background: #0ccd7de0 0% 0% no-repeat padding-box;
    font-size: 2rem;
    border-style: none;
    padding: 0.5rem 1rem;
    border-radius: 10px;
    /* margin-top: 2rem; */
    width: 12rem;
}

.revokeUserBtn {
    outline: none;
}

.toRevokeListLink:before {
    font-size: 22px;
    font-family: FontAwesome;
    font-style: normal;
    margin-left: 2px;
    margin-right: 5px;
    content: "\f0a8";
}

.toRevokeListLink {
    font-size: 22px;
    color: black;
}

.backBtnHrDiv {
    width: 90%;
}

.backBtnHrDiv {
    margin: auto;
    padding-top: 20px;
}

.revokeUserSelectDiv {
    margin-bottom: 15px;
}

.revokeRequestFooter {
    padding-top: 10px;
}

.revokeUserBtn:focus {
    outline: none;
}

.successDiv {
    color: #0f5132;
    background-color: #d1e7dd;
    border-color: #badbcc;
    height: 54px;
    width: 40rem;
    /* margin: auto; */
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 19px;
    margin-top: 12px;
    border-radius: 10px;
}

.fieldReq {
    border-color: red;
}

.selectFieldReq div {
    border-color: red;
}

.commentFieldReq {
    color: red;
}

.selectDivReq {
    color: red;
}