.basisDiv {
  height: 1px;
  width: 200px;
  box-shadow: 0px 3px 6px #00000029;
  border: solid 1px;
  color: #49a2e6;
  background-color: #49a2e6;
  transition: transform 0.2s;
}

.basisDiv:hover {
  transform: scaleY(1.5);
}

.eotDiv {
  width: 1px;
  height: 1rem;
  border: solid 1px;
  position: absolute;
  background-color: black;
  right: 20%;
  top: 41%;
  box-shadow: 0px 3px 6px #00000029;
  transition: transform 0.2s;
  /* z-index: 1; */
}

.eotDiv:hover {
  transform: scale(1.5);
}

