.statusDiv {
  border: 2px;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  position: absolute;
  left: 6.5%;
  top: 36%;
  background-color: gray;
  box-shadow: 0px 3px 6px #00000029;
  transition: transform 0.2s;
}

.statusDiv:hover {
  transform: scale(1.5);
}

