.scoringResultWrapper {
  display: flex;
  width: 50%;
  border-radius: 5px;
  border: 1px solid #e3e6f0;
  box-shadow: 0px 3px 6px #00000029;
  flex-direction: column;
}

.scoringResultContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3rem;
}

.backBtnDiv {
  display: flex;
  width: 50%;
}

.scoringResultHeader {
  display: flex;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background: #c1dbef 0% 0% no-repeat padding-box;
  padding-left: 1rem;
  width: 100%;
  height: 50px;
  align-items: center;
}

.scoringResultHeader h4 {
  color: #043964;
  font-size: 20px;
}

.scoringContent {
  display: flex;
}

.patientStatusDiv {
  width: 50%;
  padding: 2.5rem;
}

.patientStatusDiv h4 {
  text-align: center;
  padding: 1rem;
  border-radius: 8px;
}

.patientStatusDiv .noFollowUp {
  text-align: center;
  background-color: #fcecec;
  padding: 1rem;
  border-radius: 8px;
}

.toFollowUp {
  background-color: #ffd0c4;
}

.followUp {
  background-color: #c1dbef;
}

.patientIdDiv {
  width: 50%;
  padding: 1rem;
}

.idInputDisable {
  margin-left: 0;
  max-width: 20rem !important;
  font-size: 1.5rem !important;
}

.backBtn::before {
  font-family: FontAwesome;
  font-style: normal;
  margin-left: 2px;
  content: "\f0a8";
}

.backBtn div {
  display: inline;
}

.backBtn {
  width: 7rem;
  border: none;
  background-color: rgba(255, 255, 255, 0.2);
  vertical-align: middle;
}

.backBtn:focus {
  outline: none;
}

.backBtn:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

.patientIdDiv label {
  font-size: 19px;
}

.wasNotRand {
  background-color: rgba(207, 203, 203, 0.5);
}

.inEOT {
  background-color: #c1dbef;
}


.cfuCriterieColor{
  background-color:#d2f8cd;
}