.dashboardWrapper {
  max-width: 160rem;
  /* height: 9rem; */
  margin: 1rem !important;
  display: flex;
  flex-direction: column;
}

.cardsWrapper {
  display: flex;
  justify-content: space-between;
  max-width: 160rem;
  align-items: center;
  height: 13rem;
}

.subjectsCard, .randomizationCard, .inTEO, .inCFU, .notCFU {
  min-width: 19%;
  border-radius: 7px;
  border: 1px solid #e3e6f0;
  box-shadow: 0px 3px 6px #00000029;
  background-color: white;
}

.rdt_TableHeader {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.notCFU .cardHeader {
  background-color: #be2a2ac5;
}

.cardHeader {
  width: 100%;
  background: #88c1ec 0% 0% no-repeat;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  font-size: 17px;
  font-weight: bold;
  text-align: center;
  padding-top: 5px;
  padding-left: 10px;
  white-space: nowrap;
  padding-bottom: 3px;
  padding-right: 10px;
}

.dashboardTable {
  display: flex;
  flex-direction: column;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.textInSubjectCard h4 {
  text-align: center;
  padding: 10px;
  font-weight: bold;
  font-size: 24px !important;
}

.dropdwondiv {
  display: flex;
  flex-direction: column;
}

.selectbox {
  width: 10%;
  height: 27px;
  margin-top: 6px;
  margin-bottom: 22px;
}

.rdt_TableCol_Sortable {
  font-weight: 700;
}

.fhaVWy {
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-top: 0;
  border-radius: 10px 10px 0 0;
}

.dashboardWrapper {
  margin: auto;
}

.titleDate {
  display: flex;
  justify-content: space-between;
}

.title h3 {
  font-size: 22px;
  font-weight: 600;
}

.allSubjectsHeader {
  color: white;
}

.randomizedHeader {
  color: white;
  background-color: #0b3fc1b5;
}

.EOTSubjectsHeader {
  color: white;
  background-color: #1183e4c5;
}

.CFUSubjectsHeader {
  color: white;
  background-color: #078407bf;
}

.test {
  color: #696969;
  font-weight: 600;
}

.darkBlack {
  color: black;
  font-weight: 600;
}

.prejectDateDiv {
  margin-top: 2rem;
  font-size: 18px;
}