.searchDiv {
  margin: auto;
}

.searchDiv div {
  width: 35rem;
}

.searchHeader h3 {
  margin: 1rem;
}

.searchDiv div > label {
  color: #6e707e !important;
  font-size: 2rem;
}

.searchInput {
  max-width: 12rem !important;
  /* text-align: center; */
  /* margin: auto; */
  font-size: 2.5rem;
  color: #6e707e !important;
}

.searchWrapper {
  background-color: white;
  display: grid;
  grid-area: "h" "b";
  grid-template-rows: 1fr 4fr;
  width: 50%;
  margin: auto;
  border-radius: 5px;
  margin-top: 3rem;
  border: 1px solid #e3e6f0;
  box-shadow: 0px 3px 6px #00000029;
}

.searchHeader {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background: #c1dbef 0% 0% no-repeat padding-box;
  grid: "h";
  padding-left: 1rem;
}

h3 {
  color: #043964;
}

.searchDiv {
  grid: "s";
  padding: 20px;
}

.searchBtn {
  grid: "b";
  text-align: center;
  padding: 20px;
}

.searchBtn button {
  color: #fff;
  background: #1a73ba 0% 0% no-repeat padding-box;
  font-size: 2rem;
  border-style: none;
  padding: 0.5rem 1rem;
  border-radius: 10px;
  margin-top: 2rem;
}

.searchBtn button:focus {
  outline: none;
}

.loaderDiv {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 5;
}
.loaderDiv img {
  width: 150px;
}

.ReactModal__Overlay {
  background-color: rgb(40 40 40 / 75%) !important;
}

.invalidIdMessage {
  color: red;
  width: 26rem;
  text-align: left;
  margin: auto;
}

.idErrorInput {
  border-color: red;
}

.idErrorInput:focus {
  border-color: red;
}

.noPatient {
  max-width: 26rem !important;
  width: 50%;
  text-align: left;
  color: red;
  margin: auto;
}

.maskInputsDiv {
  justify-content: center;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
}

.patientSearchIdDiv {
  position: relative;
  margin-left: 25px;
}
.patientSearchIdDiv:before {
  content: '';
  position: absolute;
  right: calc(100% + 5px);
  top: 50%;
  display: block;
  width: 15px;
  height: 2px;
  font-size: 2.5rem;
  background-color: #6e707e !important;
}
.siteIdDiv {
  text-align: left;
}

.siteIdDiv label {
  font-size: 19px;
}
