.createRequestWrapper {
    /* border: solid; */
    /* min-height: 35rem; */
    width: 50%;
    margin: auto;
    /* margin-top: 3rem; */
    box-shadow: 0px 3px 6px #00000029;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

.createRequestHeader {
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    display: flex;
    /* padding: 8px 0 8px 0px; */
    flex-direction: column;
    background: #c1dbef 0% 0% no-repeat padding-box;
    /* min-height: 5rem; */
}

.createRequestHeader h4 {
    color: #043964;
    margin-left: 10px;
    font-size: 20px;
}

.createRequestMain {
    padding: 12px;
}

.createRequestMain label {
    padding-top: 10px;
}

.createRequestFooter {
    padding: 12px;
}

.createUserBtn {
    color: #fff;
    background: #0ccd7de0 0% 0% no-repeat padding-box;
    font-size: 2rem;
    border-style: none;
    padding: 0.5rem 1rem;
    border-radius: 10px;
    /* margin-top: 2rem; */
    width: 12rem;
}

.createUserBtn:focus {
    outline: none;
}

.selectAcces {
    width: 100%;
}

.backUsersListDiv {
    padding-top: 30px;
    padding-bottom: 12px;
    display: flex;
    width: 50% !important;
    margin: auto;
}

.backUsersListDiv a {
    font-size: 22px;
    color: black;
}

.backUsersListDiv:hover {
    color: #23527c;
}

.backUsersListDiv:hover .toUsersListLink {
    color: #23527c;
}

.toUsersListLink:before {
    font-size: 22px;
    font-family: FontAwesome;
    font-style: normal;
    margin-left: 2px;
    margin-right: 5px;
    content: "\f0a8";
}

.createSuccessDiv {
    color: #0f5132;
    background-color: #d1e7dd;
    border-color: #badbcc;
    height: 54px;
    width: 40rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 19px;
    border-radius: 10px;
}

.succesDivBtn {
    display: flex;
    justify-content: space-around;
}

.siteInfo, .userNameInfoDiv {
    display: flex;
    align-items: center;
}

.siteInfo label, .userNameInfoDiv label {
    margin-right: 5px;
    margin-bottom: 10px;
}

.accessLevelValid {
    color: red;
}

.selectAcces div {
    border-color: red;
}

.selectAcces div:hover {
    border-color: red;
}

.selectAccessLevelDiv {
    display: flex;
    flex-direction: column;
}

.selectAccessLevelDiv select {
    height: 34px !important;
}

.selectNone{
    display: none;
}

select option{
    height: 14px;
    font-size: 15px;
}
